body {
  font: 14px "Lucida Grande", Helvetica, Arial, sans-serif;
}
a {
  color: #00B7FF;
}
.nav,
.pagination,
.carousel,
.panel-title a {
  cursor: pointer;
}
.navbar {
  min-height: 50px;
}
.navbar .navbar-brand {
  padding-top: 10px;
  padding-bottom: 10px;
}
.navbar .navbar-brand img {
  max-height: 100%;
}
footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 20px 0;
  background-color: #F5F5F5;
}
footer p {
  margin: 0;
}
.main-logo {
  max-height: 250px;
  margin: 0 auto;
}
.tip {
  font-size: 28px;
  max-width: 30%;
}
.champion img {
  margin-right: 20px;
  float: left;
}
.matches {
  margin: 20px auto;
}
.match {
  position: relative;
  height: 125px;
  background-color: whitesmoke;
  border-radius: 5px;
  margin: 15px auto;
  padding: 15px;
}
.match.lose {
  opacity: 0.6;
}
.match .icon {
  float: left;
  height: 35px;
  width: 35px;
  margin-right: 5px;
}
.match .name {
  float: left;
  font-size: 15px;
  font-weight: 600;
}
.match .score {
  float: right;
  font-weight: 500;
}
.match .kills {
  color: forestgreen;
}
.match .deaths {
  color: red;
}
.match .assists {
  color: orange;
}
.match .build {
  cursor: pointer;
}
.match h4 {
  float: right;
  margin: -3px 0 0 10px;
}
.match h5 {
  text-transform: uppercase;
}
.item-set,
.btn-item-set {
  margin-bottom: 20px;
}
